/* mainFeedback.css */
.main-feedback {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
  }
  
  h2 {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
  }
  

  
  ul {
    list-style: none;
    padding: 0;
    margin: 20px 0;
  }
  
  li {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  span {
    font-weight: bold;
    margin-left: 10px;
  }